body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}

.app {
  background-color: #282c34;
  height: 100%;
}

.note-panel {
  height: 100vh;
}

.note-content {
  height: 100vh;
}
.panel-container {
    padding: 1%;
    height: 100%;
}

.profile-initial {
   font-size: 1.5em;
}

.profile-name {
    color: #61dafb;
}

.card {
    background-color: #282c34;
    border: 1px white solid;
    overflow: auto;
    height: 75%;
}

.note-option {
    color: #61dafb;
    width: 100%;
    margin-top: 2.5px;
    margin-bottom: 2.5px;
}

.note-option:hover, .note-option:focus, .note-option.active, .open>.dropdown-toggle.note-option {
    color: white;
    background-color: #00b3db;
}
.note-container {
    height: 100%;
}

.note-title-group {
    height: 10%;
    padding: 2%;
    font-size: 1.30em;
    font-weight: bolder;
}

.note-title-group>input {
    background-color: #282c34;
    color: #00b3db;
}

.note-title-group>input:focus, .note-body>textarea:focus {
    background-color: #282c34;
    color: white;
}

.note-title-group>input::-webkit-input-placeholder, .note-body>textarea::-webkit-input-placeholder {
    color: rgb(192, 183, 183);
}

.note-title-group>input::-ms-input-placeholder, .note-body>textarea::-ms-input-placeholder {
    color: rgb(192, 183, 183);
}

.note-title-group>input::placeholder, .note-body>textarea::placeholder {
    color: rgb(192, 183, 183);
}

.delete-btn {
    background-color: red;
    color: white;
}

.note-body {
    height: 85%;
    padding: 2%;
}

.note-body>textarea {
    background-color: #282c34;
    color: white
}

